import React, { Component } from 'react';
import { render } from 'react-dom';

import PlayerDetails from 'components/cashier/index/PlayerDetails';

class Search extends Component {
    constructor(props) {
        super(props);
        this.state = {
            playersdata: null,
            loaderPlayersData: null,
        };
       
    }

    submit(thisParam) { 
        
            this.setState({loaderPlayersData: 'Searching results...', playersdata: null});
            const formData = {
                player_id: document.getElementById('select_players').value
            };

            new Promise((resolve, reject) => {
                    fetch(SERVER_BASE_URL_FULL+'search/getplayersdetails', {
                        method: 'POST',
                        headers: {
                            'Content-Type': 'application/json',
                        },
                        body: JSON.stringify({formData})
                    }).then(response => response.json())
                        .then((data) => { 

                            if(data.length == 0){
                                this.setState({loaderPlayersData: 'No data found'});
                            }
                            else{
                               this.setState({playersdata: data});
                               this.setState({loaderPlayersData: null}); 
                            }

                        }).catch(error => {
                            
                            alert("System Exception");
                            reject(error);
                        });
            });


        
        return false;
    }
    render() {

        const { players } = this.props;
        
        return (
            
                
                <table width="30%">
                <tbody>
                    <tr>
                        <td>
                            <select name="select_players" id="select_players" aria-invalid="false">
                                {(players !== null && players.length > 0) &&
                                        players.map(player => {
                                        
                                        return (
                                            <option selected="" key={player.user_id} value={player.user_id}>{player.user_name}</option>
                                        );
                                    })
                                }
                            </select>
                        </td>
                        <td>
                            <button onClick={(e)=>this.submit()} type="button">Apply</button>
                        </td>
                    </tr>
                </tbody>
                <tbody>
                    <tr>
                        <td>
                           <table width="30%">
                            <tbody>
                                <tr>
                                    <td>
                                        ewrwrwerwerwerwerwer
                                    </td>
                                    <td>
                                        werwerwerwerwerwerwerwerwerwerwer
                                    </td>
                                </tr>
                            </tbody>
                            </table>
                        </td>
                        <td>
                            {
                                this.state.playersdata != null ? (<PlayerDetails playersdata= {this.state.playersdata} />) : null
                            }
                            {this.state.loaderPlayersData}
                        </td>
                    </tr>
                </tbody>
                </table>    
                
                
            

        );
    }
}

export default Search;
